import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/', //
            component: resolve => require(['../pages/articleList.vue'], resolve)
        },
        {
            path: '/articleList', //
            component: resolve => require(['../pages/articleList.vue'], resolve)
        },
        {
            path: '/manAccount', //
            component: resolve => require(['../pages/manAccount.vue'], resolve),
        },
        {
            path: '*', //
            redirect: '/'
        }
    ]
})



